import styled from 'styled-components'

const Score = styled.div`
  margin: 0 10px;
  padding: 5px;
  font-size: 40px;
  font-weight: bold;
  color: #bbb;

  display: flex;
  flex-direction: column;
  align-items: center;
`

export default Score




