import styled from 'styled-components'

const AppStyleWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  font-family: Helvetica;
`
export default AppStyleWrapper




