import styled from 'styled-components'

const ScoreBoard = styled.div`
  width: 100%;
  margin: 20px 0 0 0;
  padding: 10px 20px 0;
  border-top: 2px dashed #bbb;

  display: flex;
  justify-content: space-between;
`

export default ScoreBoard




