import styled from 'styled-components'

const Button = styled.button`
  color: palevioletred;
  font-size: 16px;
  margin: 10px 10px 5px;
  padding: 5px 10px;
  border: 2px solid palevioletred;
  border-radius: 3px;
  background-color: whitesmoke;
  cursor: pointer;
`

export default Button




